.logo {
  .list-reset;
  display: flex;
}

.logo__icon {
  width: 100%;
}

.logo:hover {
  opacity: 0.8;
}

.logo:active {
  opacity: 0.6;
}

.logo__link {
  margin-left: 55px;
  width: 102px;
  height: 19px;
  align-self: center;

  @media (min-width: @tablet-width) {
    margin-left: 12px;
  }

  @media (min-width: @desktop-width) {
    width: 118px;
    height: 22px;
  }
}
